@keyframes slide-from-right {
  from {
    visibility: visible;
    left: 100vw;
  }
  to {
    visibility: visible;
    left: 0vw;
  }
}
.slide-from-right {
  animation-name: slide-from-right;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

@keyframes slide-to-right {
  from {
    visibility: visible;
    left: 0vw;
  }
  to {
    visibility: hidden;
    left: 100vw;
  }
}
.slide-to-right {
  animation-name: slide-to-right;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

.project-details {
  display: flex;
  visibility: hidden;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 100vw;
  z-index: 99999;
  width: 100%;
  height: 100%;
  background-color: var(--clr-background);
  overflow-y: scroll;
}

.project-details__close-btn {
  display: block;
  color: var(--clr-text);
  width: 1.5rem;
  height: 1.5rem;
  margin-top: 2rem;
  margin-right: 2rem;
  align-self: flex-end;
  transition: 0.3s ease-in-out;
}
.project-details__close-btn:hover {
  transform: rotate(90deg) scale(1.3);
}

.project-details__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 2rem;
}

.project-details__content {
  max-width: 60ch;
}

.project-details__title {
  color: var(--clr-text);
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.project-details__icon-link-wrapper {
  display: flex;
  justify-content: left;
  gap: 1rem;
  margin-bottom: 1rem;
}

.project-details__icon {
  width: 1.5rem;
  height: 1.5rem;
}

.project-details__tools {
  color: var(--clr-accent);
  font-size: 0.75em;
}

.project-details__description {
  font-size: 0.875rem;
  color: var(--clr-text);
  margin-bottom: 2rem;
}

.project-details__subtitle {
  color: var(--clr-text);
  font-size: 1.125rem;
  margin-top: 2em;
  margin-bottom: 1em;
}

.project-details__ul {
  display: flex;
  flex-direction: column;
  gap: 1em;
  list-style-type: "👉";
  padding-left: 1em;
  margin-bottom: 2rem;
}

.project-details__list {
  font-size: 0.875rem;
  color: var(--clr-text);
}
.project-details__list::before {
  content: "";
  margin-left: 0.5em;
}

.project-details__imgs-container {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  max-width: 70ch;
}

.project-details__img-small-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}

.project-details__img-small-wrapper {
  overflow: hidden;
  flex: 1;
}

.project-details__img-small {
  width: auto;
  max-height: 80px;
  margin: 0 auto;
  cursor: pointer;
  transition: scale 0.15s;
}
.project-details__img-small:hover {
  scale: 1.05;
}

@media (min-width: 475px) {
  .project-details__close-btn {
    width: 1.875rem;
    height: 1.875rem;
  }
  .project-details__title {
    font-size: 1.875rem;
    margin-bottom: 1rem;
  }
  .project-details__icon-link-wrapper {
    gap: 1rem;
  }
  .project-details__icon {
    width: 1.875rem;
    height: 1.875rem;
  }
  .project-details__description {
    font-size: 1rem;
    margin-bottom: 2rem;
  }
  .project-details__subtitle {
    font-size: 1.25rem;
  }
  .project-details__list {
    font-size: 1rem;
  }
  .project-details__img-small {
    max-height: 120px;
  }
}
@media (min-width: 640px) {
  .project-details__imgs-container {
    gap: 0.75rem;
  }
  .project-details__img-small-container {
    gap: 0.75rem;
  }
}
