  font-size: 0.875rem;
  text-decoration: none;
  font-weight: 600;
  color: var(--clr-accent);
  letter-spacing: -0.05em;
  transition: color 0.3s;
}
.mobile-nav__link:hover {
  color: var(--clr-primary);
}

.mobile-nav__line {
  border: 1px solid var(--clr-accent);
  padding-right: 1.5em;
}

.mobile-nav__sun {
  display: block;
  color: var(--clr-accent);
  width: 1.25rem;
  height: 1.25rem;
}

.mobile-nav__resume {
  font-size: 0.875rem;
