.about {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.about__content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.about__title {
  font-size: 1.5rem;
  color: var(--clr-text);
}

.about__description {
  max-width: 60ch;
  font-size: 0.875rem;
  color: var(--clr-text);
}

.about__hr {
  border-color: var(--clr-secondary);
}

.about__subtitle {
  font-size: 0.875rem;
  color: var(--clr-text);
}

.about__ul-container {
  display: flex;
}

.about__ul {
  display: flex;
  flex-direction: column;
  gap: 1em;
  list-style-type: "👍";
  padding-left: 1em;
  flex: 1;
}

.about__list {
  font-size: 0.875rem;
  color: var(--clr-accent);
}
.about__list::before {
  content: "";
  margin-left: 0.5em;
}

