.header__menu {
  display: none;
}

.header__bars {
  display: block;
  color: var(--clr-text);
  width: 1.5rem;
  height: 1.5rem;
}

@media (min-width: 768px) {
  .header__menu {
    display: flex;
    align-items: center;
    list-style: none;
    padding: 0;
    gap: 2em;
  }
  .header__link {
    font-size: 0.75rem;
    text-decoration: none;
    font-weight: 600;
    color: var(--clr-accent);
    letter-spacing: -0.05em;
    transition: color 0.3s;
  }
  .header__link:hover {
    color: var(--clr-primary);
  }
  .header__line {
    border: 1px solid var(--clr-accent);
    padding-top: 1.5em;
  }
  .header__sun {
    display: block;
    color: var(--clr-accent);
    width: 1rem;
    height: 1rem;
  }
  .header__resume {
    font-size: 0.75rem;
  }
  .header__bars {
    display: none;
  }
}
@media (min-width: 1024px) {
  .header__link {
    font-size: 0.875rem;
  }
  .header__sun {
    width: 1.25rem;
    height: 1.25rem;
  }
