  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: 2rem;
}

.hero__img {
  border-radius: 50%;
  width: 6rem;
  height: 6rem;
}

.hero__subtitle {
  color: var(--clr-text);
  font-size: 1rem;
  line-height: 0.9;
}

.hero__title {
  color: var(--clr-primary);
  text-transform: uppercase;
  font-size: 2.25rem;
  letter-spacing: -0.05em;
  line-height: 0.9;
}

.hero__description {
  max-width: 60ch;
  color: var(--clr-text);
  font-size: 0.875rem;
}

.hero__icon-link-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.hero__icon {
  width: 1.5rem;
  height: 1.5rem;
}

@media (min-width: 475px) {
  .hero {
    gap: 1.5rem;
  }
  .hero__img {
    width: 6.5rem;
    height: 6.5rem;
  }
  .hero__subtitle {
    font-size: 1.125rem;
  }
  .hero__title {
    font-size: 3rem;
  }
  .hero__description {
    font-size: 1rem;
  }
  .hero__icon-link-wrapper {
    gap: 1.5rem;
  }
  .hero__icon {
    width: 1.875rem;
    height: 1.875rem;
  }
}
@media (min-width: 1024px) {
  .hero__img {
    width: 8rem;
    height: 8rem;
  }
  .hero__subtitle {
    font-size: 1.5rem;
  }
  .hero__title {
    font-size: 4.5rem;
  }
  .hero__description {
    font-size: 1.125rem;
  }
  .hero__icon {
    width: 2.25rem;
    height: 2.25rem;
  }
}
