  .hero {
    gap: 2rem;
  }
  .hero__img {
    width: 10rem;
    height: 10rem;
  }
  .hero__subtitle {
    font-size: 2.25rem;
  }
  .hero__title {
    font-size: 8rem;
  }
  .hero__description {
    font-size: 1.25rem;
  }
  .hero__icon-link-wrapper {
    gap: 2rem;
  }
  .hero__icon {
    width: 3rem;
    height: 3rem;
  }
